.persons {
  /* display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 20px; */
  display: flex;
  max-width: 1200px;
  margin: 0 20px 50px;
}

  div.person-item {
    background-color: #EEE8EC;
    flex-direction: column;
    padding: 2% 0 4% 0;
    align-items: center;
    box-shadow: 0px 4px 9px -3px rgba(0,0,0,0.62);
    border: 10px solid white;
    margin-bottom: 20px;
    /* margin: 20px 10px;
    flex: 1;
    min-width: 300px;
    max-width: 400px; */
  }

  
  div.person-item img {
    object-fit: contain;
    margin: 30px 0 20px 0;
    width: 200px;
    /* flex-shrink: 0;
    padding: 4%; */
  }
  
  
  div.person-info {
    color: black;
    text-align: center;
    flex-direction: column;
    align-items: flex-start;
    padding: 0 10%;
  }
  
  div.person-info h3 {
    margin: 0 auto;
  }  

  div.person-info a {
    color: black;
  }

  div.person-info p {
    color: black;
  }


@media only screen and (max-width: 1024px) {
  .persons {
      grid-column-gap: 5px;
  }
  
}

@media only screen and (max-width: 768px) {
  .persons {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
  }
  div.person-item img {
    width: 130px;
  }
} 
@media only screen and (max-width: 480px) {
  .person {
      margin: 0;
    }
  div.person-item {
    margin: 10px 0;
  }
  div.person-info h3 {
    font-size: 1.7rem;
  }
}
  
  



